.toggle-box input[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
}
.toggle-box label {
  cursor: pointer;
  text-indent: -9999px;
  width: 34px;
  height: 19px;
  display: block;
  position: relative;
  background: #c8e6ff;
  border-radius: 100px;
  box-shadow: inset -1px -1px 1px rgba(255, 255, 255, 0.5),
    inset 1px 1px 1px rgba(0, 16, 57, 0.2);
}
.toggle-box label:after {
  content: "";
  position: absolute;
  top: 2px;
  left: 3px;
  width: 15px;
  height: 15px;
  background: #fff;
  transition: 0.3s;
  border-radius: 90px;
}
.toggle-box input:checked + label {
  background: #00498b;
}
.toggle-box input:checked + label:after {
  left: calc(100% - 3px);
  transform: translateX(-100%);
}
